import React from 'react';
import { Nav, Navbar, Container } from 'react-bootstrap';
import { LinkContainer } from "react-router-bootstrap";

import './Navigation.css';

const Navigation = () => {
    return (
        <Navbar collapseOnSelect expand="lg" className="navbar-header mb-5">
            <Container>
                <LinkContainer to="/blockchain-project">
                    <Navbar.Brand className="navbar-logo p-0 m-0"><img src="http://www.tu.ac.th/uploads/main-logo.svg" width="75%" alt="" /></Navbar.Brand>
                </LinkContainer>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" className="toggle-button p-0"/>
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="ml-auto text-center link">
                        <LinkContainer to="/blockchain-project"><Nav.Link>HOME</Nav.Link></LinkContainer>
                        <LinkContainer to="/blockchain-project/about"><Nav.Link>ABOUT</Nav.Link></LinkContainer>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}

export default Navigation;